.about{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}

.about_title{
    position: relative;

}

.about_title h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}

.about_title img{
    width: 360px;
    height: 80px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.about_section{
    display: flex;
    gap: 80px;
}

.about_right{
    display: flex;
    flex-direction: column;
    gap: 80px;
}

.about_para{
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 20px;
    font-weight: 500;
}

.about_skills{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.about_skill{
    display: flex;
    gap: 50px;
    align-items: center;
    transition: 0.3s;
}

.about_skill p{
    min-width:150px;
    font-size: 20px;
    font-weight: 500;
}

.about_skill hr{
    outline: none;
    border: none;
    width: 50%;
    height: 8px;
    border-radius: 50px;
    background: linear-gradient(264deg, #DF8908 -5.09%, #B413FF 106.28%);
}


.about_achievements{
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-bottom: 80px;
}

.about_achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    transition: 0.5s;
}

.about_achievement h1{
    font-size: 60px;
    font-weight: 700;
    background: linear-gradient(264deg, #DF8908 50.41%, #B413FF 90.25%);
    background-clip:text ;
    -webkit-animation-clip:text ;
    -webkit-text-fill-color: transparent;
}

.about_achievement p {
    font-size: 18px;
    font-weight: 500;

}

.about_skill:hover{
    transform: scale(1.05);
    transition: 0.3s;
}

.about_achievement:hover{
    transform: scale(1.12);
    transition: 0.5s;
}

@media (max-width: 768px) {
    .about{
        margin: 50px 70px ;
        gap: 80px;
        align-items: start;
    }
    .about_title h1{
        font-size: 50px;
        padding: 0;
    }
    .about_title img{
        width: 230px;
        margin: -10px   -10px;
        right: -10px;
    }
    .about_section{
        flex-direction: column;
    }
    .about_left{
        display: none;

    }
    .about_para{
        font-size: 18px;
        line-height: 32px;
    }
    .about_skill{
        gap: 20px;
        align-items: center;
    }
    .about_skill p{
        font-size: 20px;
    }
    .about_skill hr{
        height: 6px;
    }

    .about_achievements{
        justify-content: space-between;
    }

    .about_achievement{
        gap: 10px;
    }

    .about_achievement h1{
        font-size: 34px;
        font-weight: 700;
    }

    .about_achievement p{
        text-align: center;
        font-size: 16px;
    }
    
}
