.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;

}

.sevices_title{
    position: relative;
}

.services h1{
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;

}
.sevices_title img{
    width: 440px;
    height: 100px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.service_format{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding: 60px;
    border-radius: 10px;
    border: 2px solid white;
    transition: 0.4s;
    cursor: pointer;
}

.service_cointainter{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
    margin-bottom: 80px;
}

.service_format h3{
    font-size: 28px;
    font-weight: 600;

}

.service_format h2{
    font-size: 38px;
    font-weight: 800;
    background:linear-gradient(270deg , #DF8908 50%, #B415FF 100.02%) ;
    background-clip:text ;
    -webkit-background-clip: text ;
    -webkit-text-fill-color: transparent;
}

.service_format p{
    color: #d4d4d4;
    font-size: 20px;
    line-height: 40px;
    max-width: 300px;

}
.services_readmore{
    display: flex;
    gap: 20px;
    align-items: center;
    margin-top: 20px;

}


.services_readmore img{
    /* width: 100px; */
    height: 60px;
}

.service_format:hover{
    border: 2px solid #ff00ff;
    background-image: linear-gradient(45deg, #3f0028, #582300);
    transform: scale(1.05);
    transition: 0.4s;
}

@media (max-width: 768px){
    .services{
        margin: 20px 70px;
        gap:70px;
        align-items: start;
    }

    .sevices_title h1{
        padding: 0;
        font-size: 50px;    
    }

    .sevices_title img{
        width: 275px;
        margin: -20px ;
    }

    .service_format{
        gap: 20px;
        padding: 32px;

    }
    .service_format h2{
        font-size: 28px;
    }

    .service_format p{
        font-size: 16px;
        line-height: 32px;
        max-width: 300px;
    }

    .services_readmore img{
        width: 40px;
    }

    .service_cointainter{
        grid-template-columns: 1fr 1fr ;
        gap: 30px;
    }
}